<template>
  <v-card>

    <template>
      <v-container grid-list-md text-xs-center>
        <v-layout row wrap mt-4>
          <v-flex class="text-xs-center" color="grey lighten-3" xs12 sm12 md12>
            <p class="display-1">DATA STOCK BARANG</p>
          </v-flex>
        </v-layout>
      </v-container>
    </template>

    <v-card-title>
      <app-add-stockbarang-dialog></app-add-stockbarang-dialog>
      <router-view :key="$route.path"></router-view>

    
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        top
        v-model="search"
      ></v-text-field>
    </v-card-title>

    <hr>

    <v-data-table
        v-bind:headers="headers"
        v-bind:items="stockbarang"
        v-bind:search="search"
      >

      <template slot="items" slot-scope="props">
        <td>
          <v-edit-dialog lazy> {{ props.item.nama_stockbarang }}
            <v-text-field
              slot="input"
              label="Edit"
              v-model="props.item.nama_stockbarang"
              single-line
              counter
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>
        <td class="text-xs-right">{{ props.item.supplier_stockbarang }}</td>
        <td class="text-xs-right">Rp. {{ props.item.harga_stockbarang }}</td>
        <td class="text-xs-right">{{ props.item.satuan_stockbarang }}</td>
        <td class="text-xs-right">{{ props.item.jumlah_stockbarang }}</td>
        <td class="text-xs-right">Rp. {{ props.item.total_harga_stockbarang }}</td>
        <td class="text-xs-right">{{ props.item.kode_stockbarang }}</td>


        <td>
          <v-spacer></v-spacer>
          <app-edit-stockbarang-dialog :stockbarang="props.item"></app-edit-stockbarang-dialog>
          <router-view :key="$route.path"></router-view>
        </td>

      </template >

      <template slot="pageText" slot-scope="{ pageStart, pageStop }">
        From {{ pageStart }} to {{ pageStop }}
      </template>
    </v-data-table>


  </v-card>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        tmp: '',
        search: '',
        pagination: {},
        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_stockbarang'
          },
          { text: 'Supplier', value: 'supplier_stockbarang' },
          { text: 'Harga', value: 'harga_stockbarang' },
          { text: 'Satuan', value: 'satuan_stockbarang' },
          { text: 'Jumlah', value: 'jumlah_stockbarang' },
          { text: 'Total Harga', value: 'total_harga_stockbarang' },
          { text: 'Kode', value: 'kode_stockbarang' },
        ],
        errorMessages: [],

        nama_stockbarang: null,
        supplier_stockbarang: null,
        harga_stockbarang: null,
        satuan_stockbarang: null,
        jumlah_stockbarang: null,
        total_harga_stockbarang: null,
        kode_stockbarang: null,
        formHasErrors: false
          
      }
    },
    created () {
      this.fetchData()
    },
    computed: {
      stockbarang () {
        return this.$store.getters.loadedStockBarang_db
      },
      form () {
        return {
          nama_stockbarang: this.nama_stockbarang,
          supplier_stockbarang: this.supplier_stockbarang,
          harga_stockbarang: this.harga_stockbarang,
          satuan_stockbarang: this.satuan_stockbarang,
          jumlah_stockbarang: this.jumlah_stockbarang,
          total_harga_stockbarang: this.total_harga_stockbarang,
          kode_stockbarang: this.kode_stockbarang
        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },

    watch: {
      name () {
        this.errorMessages = []
      }
    },

    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.jumlah_stockbarang && !this.nama_stockbarang
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateStockBarang () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

        })
        if (this.nama_stockbarang.trim() === '' || 
            this.supplier_stockbarang.trim() === '' || 
            this.harga_stockbarang.trim() === '' ||
            this.satuan_stockbarang.trim() === '' || 
            this.jumlah_stockbarang.trim() === '' || 
            //this.total_harga_stockbarang.trim() === '' || 
            this.kode_stockbarang.trim() === '' 
          ) {
          return
        }
        const stockbarangData = {
          nama_stockbarang: this.nama_stockbarang,
          supplier_stockbarang: this.supplier_stockbarang,
          harga_stockbarang: this.harga_stockbarang,
          satuan_stockbarang: this.satuan_stockbarang,
          jumlah_stockbarang: this.jumlah_stockbarang,
          total_harga_stockbarang: this.total_harga_stockbarang,
          kode_stockbarang: this.kode_stockbarang
        }
        this.$store.dispatch('createStockBarang', stockbarangData)
        this.$router.push('/stockbarang')

      }

    }
  }
</script>



