<template>
  <v-dialog max-width="500px" persistent v-model="dialog_add">
    <!-- <v-btn fab dark small color="red" accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn> -->
    <v-chip
      color="primary"
      text-color="white"
      accent slot="activator"
      class="subheading"
    >
      <v-avatar>
        <v-icon>assignment</v-icon>
      </v-avatar>
      Add Supplier
    </v-chip>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Formulir Data Supplier</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
                box dark
                label="Full Name"
                placeholder="Full Name"
                v-model="nama_supplier"
                required
                id="nama_supplier"
                :rules="[() => !!nama_supplier || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Email Address"
                placeholder="supplier@gmail.com"
                v-model="email_supplier"
                required
                id="email_supplier"
                :rules="[() => !!email_supplier || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Nomor Telepon"
                placeholder="628**********"
                v-model="telepon_supplier"
                required
                id="telepon_supplier"
                :rules="[() => !!telepon_supplier || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Nomor Whatsapp"
                placeholder="628**********"
                v-model="whatsapp_supplier"
                required
                id="whatsapp_supplier"
                :rules="[() => !!whatsapp_supplier || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
    
              <v-text-field
                box dark
                label="Alamat Rumah"
                placeholder="Jl. ..."
                :rules="[
                  () => !!alamat_supplier || 'This field is required',
                  () => !!alamat_supplier && alamat_supplier.length <= 219 || 'Address must be less than 219 characters',
                  this.addressCheck
                ]"
                multi-line
                v-model="alamat_supplier"
                id="alamat_supplier"
                counter="219"
                required
              ></v-text-field>
    
            <v-text-field
              box dark
              label="Bank supplier"
              placeholder="BRI"
              v-model="bank_supplier"
              required
              id="bank_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Rekening supplier"
              placeholder="00*"
              v-model="rek_supplier"
              required
              id="rek_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Kode supplier"
              placeholder="00*"
              v-model="kode_supplier"
              required
              id="kode_supplier"
              :rules="[() => !!kode_supplier || 'This field is required']"
              :error-messages="errorMessages"
            ></v-text-field>
    
            </v-card-text>

          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="dialog_add = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onCreateSupplier"
              >Submit</v-btn>
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',

        emailRules: [ //form isian data diri
          v => {
            return !!v || 'E-mail is required'
          },
          v => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
        ],

        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_supplier'
          },
          { text: 'Email', value: 'email_supplier' },
          { text: 'Telepon', value: 'telepon_supplier' },
          { text: 'Whatsapp', value: 'whatsapp_supplier' },
          { text: 'Alamat', value: 'alamat_supplier' },
          { text: 'Bank', value: 'bank_supplier' },
          { text: 'Rekening', value: 'rek_supplier' },
          { text: 'Kode', value: 'kode_supplier' }
        ],

        errorMessages: [],

        nama_supplier: null,
        email_supplier: null,
        telepon_supplier: null,
        whatsapp_supplier: null,
        alamat_supplier: null,
        bank_supplier: null,
        rek_supplier: null,
        kode_supplier: null,
        formHasErrors: false
      }
    },
    created () { //form isian supplier
      this.fetchData()
    },
    watch: { //form isian supplier
      name () {
        this.errorMessages = []
      }
    },
    computed: {
      supplier () {
        return this.$store.getters.loadedSupplier_db
      },
      form () {
        return {
          nama_supplier: this.nama_supplier,
          email_supplier: this.email_supplier,
          telepon_supplier: this.telepon_supplier,
          whatsapp_supplier: this.whatsapp_supplier,
          alamat_supplier: this.alamat_supplier,
          bank_supplier: this.bank_supplier,
          rek_supplier: this.rek_supplier,
          kode_supplier: this.kode_supplier
        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },

    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.alamat_supplier && !this.nama_supplier
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateSupplier () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          //this.$refs[f].validate(true)
        })
        if (this.nama_supplier.trim() === '' || 
            this.email_supplier.trim() === '' || 
            this.telepon_supplier.trim() === '' ||
            this.whatsapp_supplier.trim() === '' || 
            this.alamat_supplier.trim() === '' || 
            this.bank_supplier.trim() === '' || 
            this.rek_supplier.trim() === '' || 
            this.kode_supplier.trim() === '' 
          ) {
          return
        }
        const supplierData = {
          nama_supplier: this.nama_supplier,
          email_supplier: this.email_supplier,
          telepon_supplier: this.telepon_supplier,
          whatsapp_supplier: this.whatsapp_supplier,
          alamat_supplier: this.alamat_supplier,
          bank_supplier: this.bank_supplier,
          rek_supplier: this.rek_supplier,
          kode_supplier: this.kode_supplier
        }
        this.$store.dispatch('createSupplier', supplierData)
        this.$router.push('/supplier')

      }

    }
  }
</script>


