import * as firebase from 'firebase'

export default {
  state: {
    loadedStockBarang_db: [

    ]
  },
  mutations: {
    setLoadedStockBarang_db (state, payload) {
      state.loadedStockBarang_db = payload
    },
    createStockBarang (state, payload) {
      state.loadedStockBarang_db.push(payload)
    },
    updateStockBarang (state, payload) {
      const stockbarang = state.loadedStockBarang_db.find(stockbarang => {
        return stockbarang.id === payload.id
      })
      if (payload.nama_stockbarang) { stockbarang.nama_stockbarang = payload.nama_stockbarang }
      if (payload.supplier_stockbarang) { stockbarang.supplier_stockbarang = payload.supplier_stockbarang }
      if (payload.harga_stockbarang) { stockbarang.harga_stockbarang = payload.harga_stockbarang }
      if (payload.satuan_stockbarang) { stockbarang.satuan_stockbarang = payload.satuan_stockbarang }
      if (payload.jumlah_stockbarang) { stockbarang.jumlah_stockbarang = payload.jumlah_stockbarang }
      if (payload.total_harga_stockbarang) { stockbarang.total_harga_stockbarang = payload.total_harga_stockbarang }
      if (payload.kode_stockbarang) { stockbarang.kode_stockbarang = payload.kode_stockbarang }
    },
    deleteStockBarang (state, payload) {
      const stockbarang = state.loadedStockBarang_db.find(stockbarang => {
        return stockbarang.id === payload.id
      })
      if (payload.nama_stockbarang) { stockbarang.nama_stockbarang = null }
      if (payload.supplier_stockbarang) { stockbarang.supplier_stockbarang = null }
      if (payload.harga_stockbarang) { stockbarang.harga_stockbarang = null }
      if (payload.satuan_stockbarang) { stockbarang.satuan_stockbarang = null }
      if (payload.jumlah_stockbarang) { stockbarang.jumlah_stockbarang = null }
      if (payload.total_harga_stockbarang) { stockbarang.total_harga_stockbarang = null }
      if (payload.kode_stockbarang) { stockbarang.kode_stockbarang = null }
      if (payload.creatorId) { stockbarang.creatorId = null }
    }
  },
  actions: {
    loadStockBarang_db ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('stockbarang_db').once('value')
      .then((data) => {
        const stockbarang_db = []
        const obj = data.val()
        for (let key in obj) {
          stockbarang_db.push({
            id: key,
            nama_stockbarang: obj[key].nama_stockbarang,
            supplier_stockbarang: obj[key].supplier_stockbarang,
            harga_stockbarang: obj[key].harga_stockbarang,
            satuan_stockbarang: obj[key].satuan_stockbarang,
            jumlah_stockbarang: obj[key].jumlah_stockbarang,
            total_harga_stockbarang: obj[key].total_harga_stockbarang,
            kode_stockbarang: obj[key].kode_stockbarang,
            creatorId: obj[key].creatorId
          })
        }
        //console.log(data)
        commit('setLoadedStockBarang_db', stockbarang_db)
        commit('setLoading', false)
      })
      .catch(
        (error) => {
          console.log(error)
          commit('setLoading', false)
        }
      )
    },
    createStockBarang ({commit, getters}, payload) {
      const stockbarang = {
        nama_stockbarang: payload.nama_stockbarang,
        supplier_stockbarang: payload.supplier_stockbarang,
        harga_stockbarang: payload.harga_stockbarang,
        satuan_stockbarang: payload.satuan_stockbarang,
        jumlah_stockbarang: payload.jumlah_stockbarang,
        total_harga_stockbarang: payload.total_harga_stockbarang,
        kode_stockbarang: payload.kode_stockbarang,
        creatorId: getters.user.id
      }
      let imageUrl
      let key
      firebase.database().ref('stockbarang_db').push(stockbarang)
      .then((data) => {
        key = data.key
        return key
      })
      .then(key => {
        const filename = payload.image.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        return firebase.storage().ref('stockbarang_db/' + key + '.' + ext).put(payload.image)
      })
      .then(fileData => {
        imageUrl = fileData.metadata.downloadURLs[0]
        return firebase.database().ref('stockbarang_db').child(key).update({imageUrl: imageUrl})
      })
      .then(() => {
        commit('createStockBarang', {
          ...stockbarang,
          imageUrl: imageUrl,
          id: key
        })
      })
      .catch((error) => {
        console.log(error)
      })
      // Reach out to firebase and store it
    },
    updateStockBarangData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      if (payload.nama_stockbarang) { updateObj.nama_stockbarang = payload.nama_stockbarang }
      if (payload.supplier_stockbarang) { updateObj.supplier_stockbarang = payload.supplier_stockbarang }
      if (payload.harga_stockbarang) { updateObj.harga_stockbarang = payload.harga_stockbarang }
      if (payload.satuan_stockbarang) { updateObj.satuan_stockbarang = payload.satuan_stockbarang }
      if (payload.jumlah_stockbarang) { updateObj.jumlah_stockbarang = payload.jumlah_stockbarang }
      if (payload.total_harga_stockbarang) { updateObj.total_harga_stockbarang = payload.total_harga_stockbarang }
      if (payload.kode_stockbarang) { updateObj.kode_stockbarang = payload.kode_stockbarang }

      firebase.database().ref('stockbarang_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateStockBarang', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    deleteStockBarangData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      updateObj.nama_stockbarang = null
      updateObj.supplier_stockbarang = null
      updateObj.harga_stockbarang = null
      updateObj.satuan_stockbarang = null
      updateObj.jumlah_stockbarang = null
      updateObj.total_harga_stockbarang = null
      updateObj.kode_stockbarang = null
      updateObj.creatorId = null

      firebase.database().ref('stockbarang_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('deleteStockBarang', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    }
  },
  getters: {
    loadedStockBarang_db (state) {
      return state.loadedStockBarang_db.sort((stockbarangA, stockbarangB) => {
        return stockbarangA.id > stockbarangB.id
      })
    },
    featuredStockBarang_db (state, getters) {
      return getters.loadedStockBarang_db.slice(0, 5)
    },
    loadedStockBarang (state) {
      return (stockbarangId) => {
        return state.loadedStockBarang_db.find((stockbarang) => {
          return stockbarang.id === stockbarangId
        })
      }
    }
  }
}


