<template>
  <v-dialog max-width="500px" persistent v-model="dialog_add">
    <v-chip
      color="primary"
      text-color="white"
      accent slot="activator"
      class="subheading"
    >
      <v-avatar>
        <v-icon>assignment</v-icon>
      </v-avatar>
      Add Barang
    </v-chip>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Formulir Data Barang</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
                box dark
                label="Nama Barang"
                placeholder="Nama Barang"
                v-model="nama_stockbarang"
                required
                id="nama_stockbarang"
                :rules="[() => !!nama_stockbarang || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Supplier"
                placeholder="Nama Supplier"
                v-model="supplier_stockbarang"
                required
                id="supplier_stockbarang"
                :rules="[() => !!supplier_stockbarang || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Harga"
                placeholder="Rp. ..."
                v-model="harga_stockbarang"
                required
                id="harga_stockbarang"
                :rules="[() => !!harga_stockbarang || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              <v-text-field
                box dark
                label="Satuan"
                placeholder="pcs / unit / box / rim"
                v-model="satuan_stockbarang"
                required
                id="satuan_stockbarang"
                :rules="[() => !!satuan_stockbarang || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
              
              <v-text-field
                box dark
                label="Jumlah"
                placeholder="..."
                v-model="jumlah_stockbarang"
                required
                id="jumlah_stockbarang"
                :rules="[() => !!jumlah_stockbarang || 'This field is required']"
                :error-messages="errorMessages"
              ></v-text-field>
    
            <!-- <v-text-field
              box dark
              label="Total Harga"
              placeholder="Rp. ..."
              v-model="total_harga_stockbarang"
              id="total_harga_stockbarang"
            ></v-text-field> -->
            <v-text-field
              box dark
              label="Kode Barang"
              placeholder="00*"
              v-model="kode_stockbarang"
              required
              id="kode_stockbarang"
              :rules="[() => !!kode_stockbarang || 'This field is required']"
              :error-messages="errorMessages"
            ></v-text-field>
    
            </v-card-text>

          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="dialog_add = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onCreateStockBarang"
              >Submit</v-btn>
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',

        emailRules: [ //form isian data diri
          v => {
            return !!v || 'E-mail is required'
          },
          v => /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
        ],

        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_stockbarang'
          },
          { text: 'Supplier', value: 'supplier_stockbarang' },
          { text: 'Harga', value: 'harga_stockbarang' },
          { text: 'Satuan', value: 'satuan_stockbarang' },
          { text: 'Jumlah', value: 'jumlah_stockbarang' },
          // { text: 'Total Harga', value: 'total_harga_stockbarang' },
          { text: 'Kode', value: 'kode_stockbarang' }
        ],

        errorMessages: [],

        nama_stockbarang: null,
        supplier_stockbarang: null,
        harga_stockbarang: null,
        satuan_stockbarang: null,
        jumlah_stockbarang: null,
        total_harga_stockbarang: null,
        kode_stockbarang: null,
        formHasErrors: false
      }
    },
    created () { //form isian stockbarang
      this.fetchData()
    },
    watch: { //form isian stockbarang
      name () {
        this.errorMessages = []
      }
    },
    computed: {
      stockbarang () {
        return this.$store.getters.loadedStockBarang_db
      },
      form () {
        return {
          nama_stockbarang: this.nama_stockbarang,
          supplier_stockbarang: this.supplier_stockbarang,
          harga_stockbarang: this.harga_stockbarang,
          satuan_stockbarang: this.satuan_stockbarang,
          jumlah_stockbarang: this.jumlah_stockbarang,
          total_harga_stockbarang: this.harga_stockbarang*this.jumlah_stockbarang,
          kode_stockbarang: this.kode_stockbarang
        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },

    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.supplier_stockbarang && !this.nama_stockbarang
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateStockBarang () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          //this.$refs[f].validate(true)
        })
        if (this.nama_stockbarang.trim() === '' || 
            this.supplier_stockbarang.trim() === '' || 
            this.harga_stockbarang.trim() === '' ||
            this.satuan_stockbarang.trim() === '' || 
            this.jumlah_stockbarang.trim() === '' || 
            //this.total_harga_stockbarang.trim() === '' || 
            this.kode_stockbarang.trim() === '' 
          ) {
          return
        }
        const stockbarangData = {
          nama_stockbarang: this.nama_stockbarang,
          supplier_stockbarang: this.supplier_stockbarang,
          harga_stockbarang: this.harga_stockbarang,
          satuan_stockbarang: this.satuan_stockbarang,
          jumlah_stockbarang: this.jumlah_stockbarang,
          total_harga_stockbarang: this.harga_stockbarang*this.jumlah_stockbarang,
          kode_stockbarang: this.kode_stockbarang
        }
        this.$store.dispatch('createStockBarang', stockbarangData)
        this.$router.push('/stockbarang')

      }

    }
  }
</script>


