import * as firebase from 'firebase'

export default {
  state: {
    loadedSupplier_db: [

    ]
  },
  mutations: {
    setLoadedSupplier_db (state, payload) {
      state.loadedSupplier_db = payload
    },
    createSupplier (state, payload) {
      state.loadedSupplier_db.push(payload)
    },
    updateSupplier (state, payload) {
      const supplier = state.loadedSupplier_db.find(supplier => {
        return supplier.id === payload.id
      })
      if (payload.nama_supplier) { supplier.nama_supplier = payload.nama_supplier }
      if (payload.email_supplier) { supplier.email_supplier = payload.email_supplier }
      if (payload.telepon_supplier) { supplier.telepon_supplier = payload.telepon_supplier }
      if (payload.whatsapp_supplier) { supplier.whatsapp_supplier = payload.whatsapp_supplier }
      if (payload.alamat_supplier) { supplier.alamat_supplier = payload.alamat_supplier }
      if (payload.bank_supplier) { supplier.bank_supplier = payload.bank_supplier }
      if (payload.rek_supplier) { supplier.rek_supplier = payload.rek_supplier }
      if (payload.kode_supplier) { supplier.kode_supplier = payload.kode_supplier }
    },
    deleteSupplier (state, payload) {
      const supplier = state.loadedSupplier_db.find(supplier => {
        return supplier.id === payload.id
      })
      if (payload.nama_supplier) { supplier.nama_supplier = null }
      if (payload.email_supplier) { supplier.email_supplier = null }
      if (payload.telepon_supplier) { supplier.telepon_supplier = null }
      if (payload.whatsapp_supplier) { supplier.whatsapp_supplier = null }
      if (payload.alamat_supplier) { supplier.alamat_supplier = null }
      if (payload.bank_supplier) { supplier.bank_supplier = null }
      if (payload.rek_supplier) { supplier.rek_supplier = null }
      if (payload.kode_supplier) { supplier.kode_supplier = null }
      if (payload.creatorId) { supplier.creatorId = null }
    }
  },
  actions: {
    loadSupplier_db ({commit}) {
      commit('setLoading', true)
      firebase.database().ref('supplier_db').once('value')
      .then((data) => {
        const supplier_db = []
        const obj = data.val()
        for (let key in obj) {
          supplier_db.push({
            id: key,
            nama_supplier: obj[key].nama_supplier,
            email_supplier: obj[key].email_supplier,
            telepon_supplier: obj[key].telepon_supplier,
            whatsapp_supplier: obj[key].whatsapp_supplier,
            alamat_supplier: obj[key].alamat_supplier,
            bank_supplier: obj[key].bank_supplier,
            rek_supplier: obj[key].rek_supplier,
            kode_supplier: obj[key].kode_supplier,
            creatorId: obj[key].creatorId
          })
        }
        //console.log(data)
        commit('setLoadedSupplier_db', supplier_db)
        commit('setLoading', false)
      })
      .catch(
        (error) => {
          console.log(error)
          commit('setLoading', false)
        }
      )
    },
    createSupplier ({commit, getters}, payload) {
      const supplier = {
        nama_supplier: payload.nama_supplier,
        email_supplier: payload.email_supplier,
        telepon_supplier: payload.telepon_supplier,
        whatsapp_supplier: payload.whatsapp_supplier,
        alamat_supplier: payload.alamat_supplier,
        bank_supplier: payload.bank_supplier,
        rek_supplier: payload.rek_supplier,
        kode_supplier: payload.kode_supplier,
        creatorId: getters.user.id
      }
      let imageUrl
      let key
      firebase.database().ref('supplier_db').push(supplier)
      .then((data) => {
        key = data.key
        return key
      })
      .then(key => {
        const filename = payload.image.name
        const ext = filename.slice(filename.lastIndexOf('.'))
        return firebase.storage().ref('supplier_db/' + key + '.' + ext).put(payload.image)
      })
      .then(fileData => {
        imageUrl = fileData.metadata.downloadURLs[0]
        return firebase.database().ref('supplier_db').child(key).update({imageUrl: imageUrl})
      })
      .then(() => {
        commit('createSupplier', {
          ...supplier,
          imageUrl: imageUrl,
          id: key
        })
      })
      .catch((error) => {
        console.log(error)
      })
      // Reach out to firebase and store it
    },
    updateSupplierData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      if (payload.nama_supplier) { updateObj.nama_supplier = payload.nama_supplier }
      if (payload.email_supplier) { updateObj.email_supplier = payload.email_supplier }
      if (payload.telepon_supplier) { updateObj.telepon_supplier = payload.telepon_supplier }
      if (payload.whatsapp_supplier) { updateObj.whatsapp_supplier = payload.whatsapp_supplier }
      if (payload.alamat_supplier) { updateObj.alamat_supplier = payload.alamat_supplier }
      if (payload.bank_supplier) { updateObj.bank_supplier = payload.bank_supplier }
      if (payload.rek_supplier) { updateObj.rek_supplier = payload.rek_supplier }
      if (payload.kode_supplier) { updateObj.kode_supplier = payload.kode_supplier }

      firebase.database().ref('supplier_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('updateSupplier', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    },
    deleteSupplierData ({commit}, payload) {
      commit('setLoading', true)
      const updateObj = {}
      updateObj.nama_supplier = null
      updateObj.email_supplier = null
      updateObj.telepon_supplier = null
      updateObj.whatsapp_supplier = null
      updateObj.alamat_supplier = null
      updateObj.bank_supplier = null
      updateObj.rek_supplier = null
      updateObj.kode_supplier = null
      updateObj.creatorId = null

      firebase.database().ref('supplier_db').child(payload.id).update(updateObj)
        .then(() => {
          commit('setLoading', false)
          commit('deleteSupplier', payload)
        })
        .catch(error => {
          console.log(error)
          commit('setLoading', false)
        })
    }
  },
  getters: {
    loadedSupplier_db (state) {
      return state.loadedSupplier_db.sort((supplierA, supplierB) => {
        return supplierA.id > supplierB.id
      })
    },
    featuredSupplier_db (state, getters) {
      return getters.loadedSupplier_db.slice(0, 5)
    },
    loadedSupplier (state) {
      return (supplierId) => {
        return state.loadedSupplier_db.find((supplier) => {
          return supplier.id === supplierId
        })
      }
    }
  }
}


