import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Meetups from '@/components/Meetup/Meetups'
import CreateMeetup from '@/components/Meetup/CreateMeetup'

import Profile from '@/components/User/Profile'
import CreateProfile from '@/components/User/CreateProfile'
import Signin from '@/components/User/Signin'
//import Signup from '@/components/User/Signup'

import StockBarang from '@/components/StockBarang/StockBarang'
import KirimanBarang from '@/components/KirimanBarang/KirimanBarang'
import RequestBarang from '@/components/RequestBarang/RequestBarang'



import Meetup from '@/components/Meetup/Meetup'

import Grid from '@/components/TestTest/Grid'
import DataTable from '@/components/TestTest/DataTable'
import Link1 from '@/components/TestTest/Link1'
import Link2 from '@/components/TestTest/Link2'
import Link3 from '@/components/TestTest/Link3'
import Link4 from '@/components/TestTest/Link4'
import Link5 from '@/components/TestTest/Link5'

import DaftarCustomer from '@/components/Customer/DaftarCustomer'
import DaftarSupplier from '@/components/Supplier/DaftarSupplier'

import AuthGuard from './auth-guard'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/', 
      name: 'Home',
      component: Home
    },
    {
      path: '/meetups',
      name: 'Meetups',
      component: Meetups
    },
    {
      path: '/meetups/:id',
      name: 'Meetup',
      props: true,
      component: Meetup
    },
    
    //halaman Barang sementara dibuat
    {
      path: '/stockbarang',
      name: 'StockBarang',
      component: StockBarang
      //beforeEnter: AuthGuard
    },
    {
      path: '/requestbarang',
      name: 'RequestBarang',
      component: RequestBarang
      //beforeEnter: AuthGuard
    },
    {
      path: '/kirimanbarang',
      name: 'KirimanBarang',
      component: KirimanBarang
      //beforeEnter: AuthGuard
    },

    {
      path: '/customer',
      name: 'DaftarCustomer',
      component: DaftarCustomer
      //beforeEnter: AuthGuard
    },
    {
      path: '/supplier',
      name: 'DaftarSupplier',
      component: DaftarSupplier
      //beforeEnter: AuthGuard
    },

    //halaman test-test
    {
      path: '/grid',
      name: 'Grid',
      component: Grid
    },
    {
      path: '/datatable',
      name: 'DataTable',
      component: DataTable
    },
    {
      path: '/link1',
      name: 'Link1',
      component: Link1
    },
    {
      path: '/link2',
      name: 'Link2',
      component: Link2
    },    {
      path: '/link3',
      name: 'Link3',
      component: Link3
    },    {
      path: '/link4',
      name: 'Link4',
      component: Link4
    },    {
      path: '/link5',
      name: 'Link5',
      component: Link5
    },

    //halaman Profil belum selesai
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      beforeEnter: AuthGuard
    },
    {
      path: '/profile/new',
      name: 'CreateProfile',
      component: CreateProfile,
      beforeEnter: AuthGuard
    },

    //halaman Authurization
    {
      path: '/meetup/new',
      name: 'CreateMeetup',
      component: CreateMeetup,
      beforeEnter: AuthGuard
    },
    // {
    //   path: '/signup',
    //   name: 'Signup',
    //   component: Signup
    // },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    }
  ],
  mode: 'history' //menghapus tanda # pada url
})



