<template>
  <v-dialog max-width="500px" persistent v-model="editDialog">
    <v-btn fab dark small color="red" accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Edit Data Stock</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
              box dark
              label="Nama Barang"
              v-model="editednama_stockbarang"
              required
              id="nama_stockbarang"
            ></v-text-field>
            <v-text-field
              box dark
              label="Supplier Barang"
              placeholder="Nama Supplier"
              v-model="editedsupplier_stockbarang"
              required
              id="supplier_stockbarang"
            ></v-text-field>
            <v-text-field
              box dark
              label="Harga Barang"
              placeholder="Rp. ..."
              v-model="editedharga_stockbarang"
              required
              id="harga_stockbarang"
            ></v-text-field>
            <v-text-field
              box dark
              label="Satuan"
              placeholder="pcs / unit / box / rim"
              v-model="editedsatuan_stockbarang"
              required
              id="satuan_stockbarang"
            ></v-text-field>
  
            <v-text-field
              box dark
              label="Jumlah Barang"
              placeholder="..."
              multi-line
              v-model="editedjumlah_stockbarang"
              id="jumlah_stockbarang"
              counter="219"
              required
            ></v-text-field>
  
            <!-- <v-text-field
              box dark
              label="Total Harga"
              placeholder="Rp. ..."
              v-model="editedtotal_harga_stockbarang"
              id="total_harga_stockbarang"
            ></v-text-field> -->

            <v-text-field
              box dark
              label="Kode stockbarang"
              placeholder="00*"
              v-model="editedkode_stockbarang"
              required
              id="kode_stockbarang"
            ></v-text-field>
            
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="red--text darken-1" 
                @click="onDeleteStockBarang"
              >Delete</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="editDialog = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onSaveChanges"
              >Save</v-btn>
              
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props:['stockbarang'],
    data () {
      return {
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        dialog_add: false,
        editDialog: false,
        
        editednama_stockbarang: this.stockbarang.nama_stockbarang,
        editedsupplier_stockbarang: this.stockbarang.supplier_stockbarang,
        editedharga_stockbarang: this.stockbarang.harga_stockbarang,
        editedsatuan_stockbarang: this.stockbarang.satuan_stockbarang,
        editedjumlah_stockbarang: this.stockbarang.jumlah_stockbarang,
        editedtotal_harga_stockbarang: this.stockbarang.harga_stockbarang * this.stockbarang.jumlah_stockbarang, //this.stockbarang.total_harga_stockbarang,
        editedkode_stockbarang: this.stockbarang.kode_stockbarang
      }

    },
    methods:{
      onSaveChanges () {

        if (this.editednama_stockbarang.trim() === '' || 
            this.editedsupplier_stockbarang.trim() === '' || 
            this.editedharga_stockbarang.trim() === '' ||
            this.editedsatuan_stockbarang.trim() === '' || 
            this.editedjumlah_stockbarang.trim() === '' || 
            // this.editedtotal_harga_stockbarang.trim() === '' || 
            this.editedkode_stockbarang.trim() === '' 
          ) {
          return
        }
        this.editDialog = false
        this.$store.dispatch('updateStockBarangData', {
          id: this.stockbarang.id,
          nama_stockbarang: this.editededitednama_stockbarang,
          supplier_stockbarang: this.editedsupplier_stockbarang,
          harga_stockbarang: this.editedharga_stockbarang,
          satuan_stockbarang: this.editedsatuan_stockbarang,
          jumlah_stockbarang: this.editedjumlah_stockbarang,
          total_harga_stockbarang: this.editedharga_stockbarang * this.editedjumlah_stockbarang, //this.editedtotal_harga_stockbarang,
          kode_stockbarang: this.editedkode_stockbarang
        })

      },
      onDeleteStockBarang () {
        this.editDialog = false
        this.$store.dispatch('deleteStockBarangData', {
          id: this.stockbarang.id,
          nama_stockbarang: null,
          supplier_stockbarang: null,
          harga_stockbarang: null,
          satuan_stockbarang: null,
          jumlah_stockbarang: null,
          // total_harga_stockbarang: null,
          kode_stockbarang: null
        })
        this.$router.push('/stockbarang')

      }

    }
  }
</script>


