<template>
  <v-dialog max-width="500px" persistent v-model="editDialog">
    <v-btn fab dark small color="red" accent slot="activator">
      <v-icon>edit</v-icon>
    </v-btn>
    <v-card dark>
      <v-container>
        <v-layout row wrap>
          <v-flex xs12>
            <h3 class="primary--text">Edit Data Supplier</h3>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-text>
              <v-text-field
              box dark
              label="Full Name"
              v-model="editednama_supplier"
              required
              id="nama_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Email Address"
              placeholder="supplier@gmail.com"
              v-model="editedemail_supplier"
              required
              id="email_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Nomor Telepon"
              placeholder="628**********"
              v-model="editedtelepon_supplier"
              required
              id="telepon_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Nomor Whatsapp"
              placeholder="628**********"
              v-model="editedwhatsapp_supplier"
              required
              id="whatsapp_supplier"
            ></v-text-field>
  
            <v-text-field
              box dark
              label="Alamat Rumah"
              placeholder="Jl. Langgar 80 B, Kel. Kemiri Muka,Kec.Beji, Kota Depok, Prov. Jawa Barat"
              multi-line
              v-model="editedalamat_supplier"
              id="alamat_supplier"
              counter="219"
              required
            ></v-text-field>
  
            <v-text-field
              box dark
              label="Bank supplier"
              placeholder="BRI"
              v-model="editedbank_supplier"
              required
              id="bank_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Rekening supplier"
              placeholder="00*"
              v-model="editedrek_supplier"
              required
              id="rek_supplier"
            ></v-text-field>
            <v-text-field
              box dark
              label="Kode supplier"
              placeholder="00*"
              v-model="editedkode_supplier"
              required
              id="kode_supplier"
            ></v-text-field>
            
            </v-card-text>
          </v-flex>
        </v-layout>
        <v-divider></v-divider>
        <v-layout row wrap>
          <v-flex xs12>
            <v-card-actions>
              <v-spacer></v-spacer>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="red--text darken-1" 
                @click="onDeleteSupplier"
              >Delete</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="blue--text darken-1" 
                @click="editDialog = false"
              >Cancel</v-btn>
              <router-view :key="$route.path"></router-view>
              <v-btn 
                class="green--text darken-1" 
                @click="onSaveChanges"
              >Save</v-btn>
              
              
            </v-card-actions>
          </v-flex>
        </v-layout>
      </v-container>
    </v-card>
  </v-dialog>
</template>

<script>
  export default {
    props:['supplier'],
    data () {
      return {
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        dialog_add: false,
        editDialog: false,
        
        editednama_supplier: this.supplier.nama_supplier,
        editedemail_supplier: this.supplier.email_supplier,
        editedtelepon_supplier: this.supplier.telepon_supplier,
        editedwhatsapp_supplier: this.supplier.whatsapp_supplier,
        editedalamat_supplier: this.supplier.alamat_supplier,
        editedbank_supplier: this.supplier.bank_supplier,
        editedrek_supplier: this.supplier.rek_supplier,
        editedkode_supplier: this.supplier.kode_supplier
      }

    },
    methods:{
      onSaveChanges () {

        if (this.editednama_supplier.trim() === '' || 
            this.editedemail_supplier.trim() === '' || 
            this.editedtelepon_supplier.trim() === '' ||
            this.editedwhatsapp_supplier.trim() === '' || 
            this.editedalamat_supplier.trim() === '' || 
            this.editedbank_supplier.trim() === '' || 
            this.editedrek_supplier.trim() === '' || 
            this.editedkode_supplier.trim() === '' 
          ) {
          return
        }
        this.editDialog = false
        this.$store.dispatch('updateSupplierData', {
          id: this.supplier.id,
          nama_supplier: this.editededitednama_supplier,
          email_supplier: this.editedemail_supplier,
          telepon_supplier: this.editedtelepon_supplier,
          whatsapp_supplier: this.editedwhatsapp_supplier,
          alamat_supplier: this.editedalamat_supplier,
          bank_supplier: this.editedbank_supplier,
          rek_supplier: this.editedrek_supplier,
          kode_supplier: this.editedkode_supplier
        })

      },
      onDeleteSupplier () {
        this.editDialog = false
        this.$store.dispatch('deleteSupplierData', {
          id: this.supplier.id,
          nama_supplier: null,
          email_supplier: null,
          telepon_supplier: null,
          whatsapp_supplier: null,
          alamat_supplier: null,
          bank_supplier: null,
          rek_supplier: null,
          kode_supplier: null
        })
        this.$router.push('/supplier')

      }

    }
  }
</script>


