<template>
  <v-app id="inspire">

    <!-- navigation samping -->
    <v-navigation-drawer
      dark
      clipped
      temporary
      fixed
      v-model="sideNav"
      app
    >

    <v-list dense>
      <template v-for="(item, i) in menuItems">
        <v-layout
          row
          v-if="item.menuItems"
          align-center
          :key="i"
        >
          <v-flex xs6>
            <v-subheader v-if="item.menuItems">
              {{ item.menuItems }}
            </v-subheader>
          </v-flex>
          <v-flex xs6 class="text-xs-center">
            <a href="#!" class="body-2 black--text">EDIT</a>
          </v-flex>
        </v-layout>
        <v-list-group v-else-if="item.children" v-model="item.model" no-action>
          <v-list-tile slot="item" @click="" :to="item.link">
            <v-list-tile-action>
              <v-icon>{{ item.model ? item.icon : item['icon-alt'] }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-list-tile
            v-for="(child, i) in item.children"
            :key="i"
            @click=""
            :to="child.link"
          >
            <v-list-tile-action v-if="child.icon">
              <v-icon>{{ child.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>
                {{ child.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list-group>
        <v-list-tile v-else @click="" :to="item.link">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
              {{ item.text }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </template>

        <v-list-tile v-if="userIsAuthenticated" @click="onLogout">
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>Logout</v-list-tile-content>

        </v-list-tile>
  

      </v-list>
    </v-navigation-drawer>
    <!-- navigation samping -->


    <!-- navigation atas -->
    <v-toolbar color="primary" dark fixed app>
      <v-toolbar-side-icon @click.stop="sideNav = !sideNav"></v-toolbar-side-icon>
      <v-toolbar-title>
        <router-link to="/" tag="span" style="cursor: pointer">Lapas Mart</router-link>
      </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-toolbar-items class="hidden-xs-only">

        <!-- <v-btn v-if="userIsAuthenticated" flat to="/supplier">
          <v-icon left>person</v-icon>
          Supplier
        </v-btn>
        <v-btn v-if="userIsAuthenticated" flat to="/customer">
          <v-icon left>face</v-icon>
          Customer
        </v-btn> -->
        
<!-- 
        <v-menu open-on-hover v-if="userIsAuthenticated" offset-y>
          <v-btn flat slot="activator">
            <v-icon left>supervisor_account</v-icon>
            Meetups
          </v-btn>
          <v-list>
            <v-list-tile icon="supervisor_account" title="meetups" to="/meetups">
              <v-list-tile-title>View Meetups</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="room" title="create_meetup" to="/meetup/new">
              <v-list-tile-title>Create Meetups</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu> -->


        <v-menu open-on-hover v-if="userIsAuthenticated" offset-y>
          <v-btn flat slot="activator">
            <v-icon left>autorenew</v-icon>
            Test Test
          </v-btn>
          <v-list>
            
            <v-list-tile icon="send" title="grid" to="/grid">
              <v-list-tile-title>Page Grid</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="datatable" to="/datatable">
              <v-list-tile-title>Data Table</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link1" to="/link1">
              <v-list-tile-title>Link1</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link2" to="/link2">
              <v-list-tile-title>Link2</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link3" to="/link3">
              <v-list-tile-title>Link3</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link4" to="/link4">
              <v-list-tile-title>Link4</v-list-tile-title>
            </v-list-tile>
            <v-list-tile icon="send" title="Link5" to="/link5">
              <v-list-tile-title>Link5</v-list-tile-title>
            </v-list-tile>


          </v-list>
        </v-menu>

        <!-- <v-btn v-if="!userIsAuthenticated" flat to="/signup">
          <v-icon left>face</v-icon>
          Sign Up
        </v-btn> -->
        <v-btn v-if="!userIsAuthenticated" flat to="/signin">
          <v-icon left>lock_open</v-icon>
          Sign In
        </v-btn>

        <v-btn v-if="userIsAuthenticated" flat @click="onLogout">
          <v-icon left>exit_to_app</v-icon>
          Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-content>
      
      <router-view></router-view>

    </v-content>
    <v-footer color="primary" app>
        <v-spacer></v-spacer>
        <span class="white--text">Lapas Mart &copy; 2018</span>
    </v-footer>
  </v-app>
</template>

<script>
/* eslint-disable */
export default {
    data () {
      return {
        dialog: false,
        sideNav: null,
        model: false,

      }
    },
    computed: {
      menuItems () {
        let menuItems = [




          { icon: 'lock_open', text: 'Sign In', link: '/signin' }
          // { icon: 'face', text: 'Sign Up', link: '/signup' },

          // { icon: 'chat_bubble', text: 'Send feedback' },
          // { icon: 'help', text: 'Help' }

        ]
        if (this.userIsAuthenticated) {
          menuItems = [

            { icon: 'fa-home', link: '/home', text: 'Home' },
            { icon: 'fa-sellsy', link: '/stockbarang', text: 'Stock Barang' },
            { icon: 'fa-fax', link: '/requestbarang', text: 'Request Barang' },
            { icon: 'fa-ship', link: '/kirimanbarang', text: 'Kiriman Barang' },

            {
              icon: 'keyboard_arrow_down',
              'icon-alt': 'keyboard_arrow_down',
              text: 'Pembelian Barang',
              model: true,
              children: [
                { icon: 'add', link: '', text: 'Transaksi Pembelian' },
                { icon: 'add', link: '', text: 'Semua Pembelian' },
                { icon: 'add', link: '', text: 'Rekap Laporan Pembelian' }
              ]
            },

            {
              icon: 'keyboard_arrow_down',
              'icon-alt': 'keyboard_arrow_down',
              text: 'Penjualan Barang',
              model: false,
              children: [
                { icon: 'add', link: '', text: 'Transaksi Penjualan' },
                { icon: 'add', link: '', text: 'Semua Penjualan' },
                { icon: 'add', link: '', text: 'Rekap Laporan Penjualan' }
              ]
            },
            
            { icon: 'fa-address-card', link: '/customer', text: 'Customer' },
            { icon: 'fa-rocket', link: '/supplier', text: 'Supplier' },

            { icon: 'supervisor_account', text: 'Meetups', link: '/meetups' }

          ]
        }
        return menuItems
      },



      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      }
    },
    methods: {
      onLogout () {
        this.$store.dispatch('logout')
      }
    },
    props: {
      source: String
    }
  }
</script>



