<template>
  <v-card>

    <template>
      <v-container grid-list-md text-xs-center>
        <v-layout row wrap mt-4>
          <v-flex class="text-xs-center" color="grey lighten-3" xs12 sm12 md12>
            <p class="display-1">DATA SUPPLIER</p>
          </v-flex>
        </v-layout>
      </v-container>
    </template>

    <v-card-title>
      <app-add-supplier-dialog></app-add-supplier-dialog>
      <router-view :key="$route.path"></router-view>

    
      <v-spacer></v-spacer>
      <v-text-field
        append-icon="search"
        label="Search"
        single-line
        hide-details
        top
        v-model="search"
      ></v-text-field>
    </v-card-title>

    <hr>

    <v-data-table
        v-bind:headers="headers"
        v-bind:items="supplier"
        v-bind:search="search"
      >

      <template slot="items" slot-scope="props">
        <td>
          <v-edit-dialog lazy> {{ props.item.nama_supplier }}
            <v-text-field
              slot="input"
              label="Edit"
              v-model="props.item.nama_supplier"
              single-line
              counter
              :rules="[max25chars]"
            ></v-text-field>
          </v-edit-dialog>
        </td>
        <td class="text-xs-right">{{ props.item.email_supplier }}</td>
        <td class="text-xs-right">{{ props.item.telepon_supplier }}</td>
        <td class="text-xs-right">{{ props.item.whatsapp_supplier }}</td>
        <td class="text-xs-right">{{ props.item.alamat_supplier }}</td>
        <td class="text-xs-right">{{ props.item.bank_supplier }}</td>
        <td class="text-xs-right">{{ props.item.rek_supplier }}</td>
        <td class="text-xs-right">{{ props.item.kode_supplier }}</td>


        <td>
          <v-spacer></v-spacer>
          <app-edit-supplier-dialog :supplier="props.item"></app-edit-supplier-dialog>
          <router-view :key="$route.path"></router-view>
        </td>

      </template >

      <template slot="pageText" slot-scope="{ pageStart, pageStop }">
        From {{ pageStart }} to {{ pageStop }}
      </template>
    </v-data-table>


  </v-card>
</template>

<script>
  export default {
    data () {
      return {
        dialog_add: false,
        max25chars: (v) => v.length <= 25 || 'Input too long!',
        tmp: '',
        search: '',
        pagination: {},
        headers: [
          {
            text: 'Nama',
            align: 'left',
            sortable: true,
            value: 'nama_supplier'
          },
          { text: 'Email', value: 'email_supplier' },
          { text: 'Telepon', value: 'telepon_supplier' },
          { text: 'Whatsapp', value: 'whatsapp_supplier' },
          { text: 'Alamat', value: 'alamat_supplier' },
          { text: 'Bank', value: 'bank_supplier' },
          { text: 'Rekening', value: 'rek_supplier' },
          { text: 'Kode', value: 'kode_supplier' }
        ],
        errorMessages: [],

        nama_supplier: null,
        email_supplier: null,
        telepon_supplier: null,
        whatsapp_supplier: null,
        alamat_supplier: null,
        bank_supplier: null,
        rek_supplier: null,
        kode_supplier: null,
        formHasErrors: false
          
      }
    },
    created () {
      this.fetchData()
    },
    computed: {
      supplier () {
        return this.$store.getters.loadedSupplier_db
      },
      form () {
        return {
          nama_supplier: this.nama_supplier,
          email_supplier: this.email_supplier,
          telepon_supplier: this.telepon_supplier,
          whatsapp_supplier: this.whatsapp_supplier,
          alamat_supplier: this.alamat_supplier,
          bank_supplier: this.bank_supplier,
          rek_supplier: this.rek_supplier,
          kode_supplier: this.kode_supplier,
        }
      },
      userIsAuthenticated () {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
    },

    watch: {
      name () {
        this.errorMessages = []
      }
    },

    methods: {
      fetchData () {
       // fetch data
       
      },
      addressCheck () {
        this.errorMessages = this.alamat_supplier && !this.nama_supplier
          ? ['Hey! I\'m required']
          : []

        return true
      },
      resetForm () {
        this.errorMessages = []
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          this.$refs[f].reset()
        })
      },
      onCreateSupplier () {
        this.dialog_add = false
        this.formHasErrors = false

        Object.keys(this.form).forEach(f => {
          if (!this.form[f]) this.formHasErrors = true

          //this.$refs[f].validate(true)
        })
        if (this.nama_supplier.trim() === '' || 
            this.email_supplier.trim() === '' || 
            this.telepon_supplier.trim() === '' ||
            this.whatsapp_supplier.trim() === '' || 
            this.alamat_supplier.trim() === '' || 
            this.bank_supplier.trim() === '' || 
            this.rek_supplier.trim() === '' || 
            this.kode_supplier.trim() === '' 
          ) {
          return
        }
        const supplierData = {
          nama_supplier: this.nama_supplier,
          email_supplier: this.email_supplier,
          telepon_supplier: this.telepon_supplier,
          whatsapp_supplier: this.whatsapp_supplier,
          alamat_supplier: this.alamat_supplier,
          bank_supplier: this.bank_supplier,
          rek_supplier: this.rek_supplier,
          kode_supplier: this.kode_supplier
        }
        this.$store.dispatch('createSupplier', supplierData)
        this.$router.push('/supplier')

      }

    }
  }
</script>



